* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  outline: none;
  font-family: sans-serif, Arial, Helvetica, sans-serif;
  font-weight: normal;
  -webkit-transition: all 0.4s cubic-bezier(0.38, 1.15, 0.7, 1.1);
  transition: all 0.4s cubic-bezier(0.38, 1.15, 0.7, 1.1);
}

body {
  text-align: center;
  overflow: hidden;
  background-image: url(https://images.pexels.com/photos/3980364/pexels-photo-3980364.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: black;
  height: 100vh;
}

body .overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: -5;
}
body .overlayTest {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.829);
  z-index: 999999999;
}
body .overlayTest i {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 999999999;
  color: #fff;
}

.container {
  -webkit-box-shadow: 0 0 6px #fff;
  box-shadow: 0 0 6px #fff;
  color: #fff;
  margin: 30px auto;
  max-width: 800px;
  background-color: transparent;
  padding-left: 15px;
  padding-right: 15px;
  border-radius: 5px;
}
@media (max-width: 700px) {
  html {
    font-size: 95%;
  }
}

@media (max-width: 500px) {
  html {
    font-size: 90%;
  }
  .container {
    margin: 0;
    box-shadow: none;
  }
}

.alert {
  width: 15rem;
  height: 50px;
  line-height: 50px;
  background-color: #e91e63c2;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  border-radius: 6px;
  color: #fff;
  font-size: 20px;
  visibility: hidden;
  z-index: 999;
}
.alert.active {
  visibility: visible;
}
